import React, { Component } from "react";
import { Link } from "gatsby";
import { Helmet } from "react-helmet"

class BlogTile extends Component {

    render() {
        return (
            
            
                <div className="blog-item">
                    <Link to={this.props.page} className="blog-item" >
                        <h3>{this.props.title}</h3>
                        <span className="date">Posted on: {this.props.pubDate}</span>
                    </Link>
                    <a href={this.props.link} target="_blank noopener">
                        <span className="icon solid fa-link"></span>
                    </a>
                                           

                    <div className={"blog_content"} dangerouslySetInnerHTML={{ __html: `<div> ${this.props.content} </div>` }} />

                    <Helmet>
                        <meta charSet="utf-8" />
                        <title>{this.props.title}</title>
                        <link rel="canonical" href={"https://naxrohan.github.io/" + this.props.page} />
                    </Helmet>
                    
                </div>
            

        );
    }
}

export default BlogTile;