import React from "react"
import Layout from "../components/layout"
import BlogTile from "../components/BlogTitle";
import BlogPageListing from "../components/BlogPageListing"
import { graphql } from "gatsby"
import Moment from "moment"
import { Helmet } from "react-helmet"

export default ({ data }) => (
    <Layout >
        <section id="top" className="two">
            <div className="container">
                <p>Welcome to my web Blog and a showcase of my Apps.</p>

                <BlogPageListing >
                    {

                        data.allBlogDataJson.nodes[0].rss.channel.item
                            .filter(function (itx) { return itx.pubDate !== "" && itx.wp_post_type === "post"; })
                            .map((item, i) =>
                                <BlogTile
                                    key={i}
                                    title={item.title}
                                    link={item.link}
                                    pubDate={new Moment(item.pubDate).format('MMMM Do YYYY')}
                                    content={item.content_encoded._xcdata}
                                    page={item.wp_post_name}
                                />
                            )
                    }
                </BlogPageListing>

            </div>
            <Helmet>
                <meta charSet="utf-8" />
                <title>Home Page</title>
                <link rel="canonical" href="https://naxrohan.github.io/" />
                <meta name="google-site-verification" content="LWbsCfponSjEXdzsH_l3iwW_gc9ABQXz1aD7rIqLgeA" />
            </Helmet>
        </section>
    </Layout>
)

export const result = graphql`
        query {
            allBlogDataJson {
                nodes {
                    rss {
                        channel {
                            item {
                                title
                                link
                                wp_post_type
                                wp_post_name
                                pubDate
                                content_encoded {
                                    _xcdata
                                }
                            }
                        }
                    }
                }
            }
        }
    `